import {
    Menu,MenuButton,MenuList,MenuItem,MenuItemOption,MenuGroup,MenuOptionGroup,MenuDivider, IconButton,
  } from '@chakra-ui/react'
  import { BrowserRouter } from "react-router-dom";
  import { HashLink as Link} from "react-router-hash-link"
  import Logo from "../image/n.jpg"
  import { HamburgerIcon} from '@chakra-ui/icons'
export default function Nav_ham(){
    const DownloadResume = () => {
        // using Java Script method to get PDF file
        fetch('Nikhil-resume.pdf').then(response => {
            response.blob().then(blob => {
                // Creating new object of PDF file
                const fileURL = window.URL.createObjectURL(blob);
                // Setting various property values
                let alink = document.createElement('a');
                alink.href = fileURL;
                alink.download = 'Nikhil-resume.pdf';
                alink.click();
            })
        })
    }
    return (
        <div className='Con'>
         <div className="Logo_box_hamburg" ><img src={Logo} alt="brand_logo" className="brand_logo" /></div>
         <div className="hambargur_comp">

        <Menu>
  <MenuButton
    as={IconButton}
    aria-label='Options'
    icon={<HamburgerIcon />}
    variant='outline'
  />
  <MenuList>
    <BrowserRouter>
    <Link to={"#home"} smooth >
    <MenuItem >
      <p>Home</p>
    </MenuItem>
      </Link>

    <Link to={"#about"} smooth >
    <MenuItem >
      <p>About</p>
    </MenuItem>
    </Link>
    
    <Link to={"#skill"} smooth ><MenuItem ><p>Skill</p></MenuItem></Link>
    
    <Link to={"#project"} smooth >
    <MenuItem>
      <p>Project</p>
    </MenuItem>
      </Link>

    <Link to={"#contect"} smooth >
    <MenuItem>
      <p>Contact</p>
    </MenuItem>
      </Link>

    <MenuItem onClick={DownloadResume}>
    <p >Resume</p>
    </MenuItem>
    </BrowserRouter>
  </MenuList>
</Menu>

</div>
        </div>
    )
}